* {
  margin: 0px;
  padding: 0px;
}

/* header css */
.mn {
  display: flex;
  align-items: center;
  background-color: gray;
  justify-content: center;
}

.ul1 {
  display: flex;
}

.ul1 li {
  margin-left: 20px;
  list-style: none;
  font-size: 25px;
  color: white;
}

/* section */
.secmain {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  flex-wrap: wrap;
  
}

.section {
  width: 300px;
  height: fit-content;
  flex-wrap: wrap;
  background-color: rgb(240, 240, 133);

}

.section img {
  width: 100%;
}

.section h2 {
  text-align: center;
}
.section .avalable{text-align: center;}


/* footer */
.ftmain {
  background-color: dimgrey;
  text-align: center;
}


/* state */

.box {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  background-color: darkolivegreen;
  height: 500px;
}


#btn {
  display: flex;
  justify-content: center;
  padding: 10px;
  gap: 10px;
}

#btn button {
  background-color: black;
  color: white;
  padding: 10px;
}

.redcolor {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  background-color: red;
  height: 500px;
}

.yellowcolor {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  background-color: yellow;
  height: 500px;
}

.greencolor {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  background-color: green;
  height: 500px;
}

/* img change */
.mainimg{justify-content: center;
          display: flex;}

.changeimg{
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 15px;
}

/* quntity */
.qunt{
  display: flex;
  font-size: 20px;
  justify-content: center;
}

.qunt button{
  padding: 6px;
  font-size: 15px;
  width: 30px;
  background-color: black;
  color: white;
}

.qunt span{padding: 6px;}